}

* {
  margin: 0;
  padding: 0;
}

.App {
  background-color: #F6F6F2;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
}
.App__content {
  width: 75%;
}
* {
  margin: 0;
  padding: 0;
}

.header {
  border-bottom: 1px solid #6FB3B8;
  width: 100%;
  height: 52px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  margin-bottom: 15px;
}
.header__left {
  font-size: 30px;
  padding-bottom: 6px;
}
.header__left-icon {
  color: #6FB3B8;
  margin-right: 10px;
  text-decoration: none;
}
.header__left-icon:hover {
  color: #C2EDCE;
}
.header__left-icon:visited {
  text-decoration: none;
}
.header__center {
  font-size: 28px;
  font-family: "Poppins", sans-serif;
  color: #6FB3B8;
  font-weight: 600;
}
* {
  margin: 0;
  padding: 0;
}

.home {
  height: 100%;
}
.home__content {
  height: 92%;
  font-family: "Poppins", sans-serif;
  color: #26474E;
  font-size: 24px;
  text-align: center;
}
.home__content-landing {
  width: 50%;
}
.home__content-landing .title {
  text-align: center;
}
.home__content-landing .text-wrapper .text1 {
  font-size: 32px;
  opacity: 0;
  -webkit-animation: an1 6s infinite;
          animation: an1 6s infinite;
  text-align: center;
}
.home__content-landing .text-wrapper .text2 {
  font-size: 32px;
  opacity: 0;
  -webkit-animation: an2 6s infinite;
          animation: an2 6s infinite;
  position: absolute;
  text-align: center;
}
.home__content-landing .text-wrapper .text3 {
  font-size: 32px;
  opacity: 0;
  -webkit-animation: an3 6s infinite;
          animation: an3 6s infinite;
  position: absolute;
  text-align: center;
}
@-webkit-keyframes an1 {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
}
@keyframes an1 {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
}
@-webkit-keyframes an2 {
  30% {
    opacity: 0;
  }
  45% {
    opacity: 1;
  }
  60% {
    opacity: 0;
  }
}
@keyframes an2 {
  30% {
    opacity: 0;
  }
  45% {
    opacity: 1;
  }
  60% {
    opacity: 0;
  }
}
@-webkit-keyframes an3 {
  60% {
    opacity: 0;
  }
  75% {
    opacity: 1;
  }
  90% {
    opacity: 0;
  }
}
@keyframes an3 {
  60% {
    opacity: 0;
  }
  75% {
    opacity: 1;
  }
  90% {
    opacity: 0;
  }
}
.home__content-landing .end {
  text-align: center;
}
.about__row1 {
  width: 100%;
  margin-bottom: 15px;
}
.about__row1-content {
  background-color: #388087;
  box-shadow: 5px 5px 15px 5px #C9C7C7;
  border-radius: 15px;
}
.about__row1-content-left img {
  width: 65%;
  height: 65%;
  border-radius: 50%;
}
.about__row1-content-right {
  margin: 10px;
  color: #6FB3B8;
}
.about__row2 {
  width: 100%;
  margin-bottom: 15px;
  color: #6FB3B8;
}
.about__row2-content2 {
  box-shadow: 5px 5px 15px 5px #C9C7C7;
  background-color: #388087;
  border-radius: 15px;
}
.about__row2-content2 .icon {
  width: 100%;
}
.about__row2-content2 .icon .icons {
  max-width: 160px;
  max-height: 160px;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  min-width: 10px;
  margin: auto;
}
